import Vue from 'vue'
import Vuex from 'vuex'
import db from './firebase'
import router from './router'

Vue.use(Vuex)
var fire = require("firebase/app");

export default new Vuex.Store({
  state: {
    Bibliotecas: [],
    Ptolectura: [],
    usuario:''
  },
  mutations: {
    setBibliotecas(state,Bibliotecas){
      state.Bibliotecas=Bibliotecas
    },
    setPtolectura(state,Ptolectura){
      state.Ptolectura=Ptolectura
    },
    setUsuario(state,usuario){
      state.usuario=usuario
    },
    Actualizar(state,id){
      state.Bibliotecas=state.Bibliotecas.filter( doc=>{
        return doc.id
      })
    },
  },
  actions: {
    getBibliotecas({commit}){
      const Bibliotecas =  []
      db.collection('Bibliotecas').get()
      .then(snapshot => {
        snapshot.forEach(doc=>{
          let biblio = doc.data();
          biblio.id=doc.id
          Bibliotecas.push(biblio)
        })
      })
      commit('setBibliotecas',Bibliotecas)
    },
    getPtolectura({commit}){
      const Ptolectura =  []
      db.collection('Ptolectura').get()
      .then(snapshot => {
        snapshot.forEach(doc=>{
          let pto = doc.data();
          pto.id=doc.id
          Ptolectura.push(pto)
        })
      })
      commit('setPtolectura',Ptolectura)
    },
    registro({commit},user){
      fire.auth().createUserWithEmailAndPassword(user.usuario,user.password)
      .then(res=>{
        fire.auth().signInWithEmailAndPassword(user.usuario,user.password)
        commit('setUsuario',{usuario:res.user.email,uid:res.user.uid})
        router.push('/')
        window.scrollTo(0,0);
      }).catch((error)=>{
        alert('Oops '+error.code);
        })
    },
    ingreso({commit},user){
      fire.auth().signInWithEmailAndPassword(user.usuario,user.password)
      .then(res=>{
        commit('setUsuario',{usuario:res.user.email,uid:res.user.uid})
        router.push('/')
        window.scrollTo(0,0);
        console.log('Bienvenido '+user.usuario);
      }).catch((error)=>{
        alert('Oops Usuario o contraseña equivocada');
        })
    },
    addBibiliotecas({commit},Bib){
      db.collection('Bibliotecas').add({
        Title: Bib[0].titulo,
        direccion: Bib[0].direccion,
        thumbnail: "https://firebasestorage.googleapis.com/v0/b/bogolibros-71163.appspot.com/o/Newlib.png?alt=media&token=dc5961f4-1b86-418f-ae78-36b266d91f40",
        Telefono:  Bib[0].telefono
      })
      .then(doc=>{
        router.push('/bibliotecas')
      })
    //  commit('Actualizar',Bibliotecas)
    },
    salir({commit}){
      fire.auth().signOut()
      commit('setUsuario',null)
      router.push('/')
      window.scrollTo(0,0);
    }
  },
  getters:{
    usuarioOn(state){
      if(state.usuario=== null|| state.usuario === '' || state.usuario=== undefined){
        return false
      }else{
        return true
      }
    }
  }
})
