<template>
  <div class="home ">
    <cabecera></cabecera>
    <Slide><a id="home" >
        <i class="fas fa-home"></i>
          <span>Inicio</span>
        </a>
        <a id="Bogolibros" >
          <i class="fas fa-bold"></i>
          <span>Bogolibros</span>
        </a>
        <a id="Bibliotecas">
          <i class="fas fa-book"></i>
          <span>Bibliotecas</span>
        </a>
        <a id="Puntos" >
          <i class="fas fa-map-marker-alt"></i>
          <span>Puntos de lectura</span>
        </a>
        <a id="Conocenos" >
          <i class="fas fa-handshake"></i>
          <span>Conocenos</span>
        </a>
        <a id="Contacto" >
          <i class="far fa-envelope"></i>
          <span>Contacto</span>
        </a>
      </Slide>
    <Principal></Principal>
    <infogrupo></infogrupo>
    <Formulario></Formulario>
    <FooterB></FooterB>
  </div>
</template>

<script>
// @ is an alias to /src
import cabecera from '@/components/cabecera.vue'
import Principal from '@/components/Principal.vue'
import Formulario from '@/components/Formulario.vue'
import FooterB from '@/components/FooterB.vue'
import infogrupo from '@/components/infogrupo.vue'
import { Slide } from 'vue-burger-menu'

const $ = require('jquery')
window.$ = $

export default {
  name: 'home',
  components: {
    cabecera,
    Principal,
    Formulario,
    FooterB,
    infogrupo,
    Slide
  },
   mounted(){
    this.$nextTick(()=>{
      $(document).ready(function(){
        $(window).scroll(function(){
            if( $(this).scrollTop() > 75 ){
              $('.bm-burger-button').slideDown(100);
            } else {
              $('.bm-burger-button').slideUp(300);
            }
              if( $(this).scrollTop() > 5000 ){
              $('.bm-burger-bars').css("background-color" , "#dcbd49");
            } else {
              $('.bm-burger-bars').css("background-color" , "#373a47");
            }
          });
          $("#home").click(function(){
           $('html,body').animate({
          scrollTop:'0'},2000
       );
      });
          $("#Bogolibros").click(function(){
           $('html,body').animate({
           scrollTop:$("#tre").offset().top},2000
       );
      });
          $("#Bibliotecas").click(function(){
           $('html,body').animate({
           scrollTop:$("#book").offset().top-150},2000
       );
      });
          $("#Puntos").click(function(){
           $('html,body').animate({
           scrollTop:$("#pte").offset().top-50},2000
       );
      });
          $("#Conocenos").click(function(){
           $('html,body').animate({
           scrollTop:$("#FondoConoc").offset().top-20},2000
       );
      });
            $("#Contacto").click(function(){
           $('html,body').animate({
           scrollTop:$("#contact").offset().top-100},2000
       );
      });
      });
    });
  }
}
</script>

<style>
.home{
  background-image: url("../assets/imgs/fondo.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.bm-burger-button{
  left:25px;
  top:20px;
  position:fixed;
  display:none;
  z-index: 999;
}
#home:hover{
 color:rgb(253, 57, 57);
 cursor: pointer;
}
#Bogolibros:hover{
  color:rgb(0, 140, 255);
  cursor: pointer;
}
#Bibliotecas:hover{
  color:rgb(255, 242, 65);
  cursor: pointer;
}
#Puntos:hover{
  color:rgb(245, 93, 23);
  cursor: pointer;
}
#Conocenos:hover{
  color:rgb(245, 214, 128);
  cursor: pointer;
}
#Contacto:hover{
  color:rgb(174, 236, 231);
  cursor: pointer;
}
@media only screen and (max-width: 375px) {
 .home{
    width: 100%;
  }
}
@media only screen and (max-width: 425px) {
 .home{
     background-image: url("../assets/imgs/fondoSIN.png");
  }
}
</style>

