<template>
    
<section id="contact" class="tm-section  ">
 <div class="ajuste">
    <header class="titu "><h3 class=" tm-section-title tm-margin-b-30" id="tituloform">Contacto</h3></header>
  
  <div class="formu ">
    
    <form action="https://formspree.io/contacto.bogolibros@gmail.com" method="POST" class="contact-form" target="_blank" autocomplete="off" id="f">
        <div class="form-group">
            <input name="Nombre" type="text" id="contact_name" onfocus="this.value=''"  class="form-control" placeholder="Nombre:"  required/>
        </div>
        <div class="form-group">
            <input name="Email" type="email" id="contact_email" onfocus="this.value=''" aria-describedby="emailHelp" class="form-control" placeholder="Email:"  required/>
        </div>
        <div class="form-group">
            <textarea name="Mensaje" id="contact_message" onfocus="this.value=''" class="form-control" rows="6" placeholder="Mensaje:" required></textarea>
        </div>                                          
        <button type="submit" class=" tm-button" id="btnform"  > ENVIAR</button>
    </form>    
    
  </div>
 </div>     
</section>

</template>

<script>
const $ = require('jquery')
window.$ = $
export default {
    mounted(){
    this.$nextTick(()=>{
    $(document).ready(function(){
    $("#btnform").click(function(){
         window.scrollTo(0,0);
         if( window.scrollTo(0,0)){
        $("#contact_name").val('');
        $("#contact_email").val('');
        $("#contact_message").val('');}
      });
    });
    });
  }
}
</script>

 

<style>
#btnform{
color:black;
background-color: #dcbd49;
font-size: 15px;
padding: 6px 60px;
border: none;
border-radius: 15px;
margin-top: 2%;
}
#btnform:hover{
background-color: #998331;
}
#contact_name, #contact_email, #contact_message{
    color: #dcbd49;
    border-color: #dcbd49;
    border-radius: 12px;
    background-color:transparent;
}
input::-webkit-input-placeholder {
    color:rgba(177, 151, 59, 0.712) !important;
}
input::-moz-placeholder {
    color:rgba(177, 151, 59, 0.712) !important;
}
