    color:rgba(177, 151, 59, 0.712) !important;
}
#contact{
color:#dcbd49;
padding-bottom: 5%;
}
.ajuste{
     display:flex;
     flex-direction: column;
     align-items: center;
}
.titu{
    display: block;
